import Divider from '@mui/material/Divider';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Typography from '@mui/material/Typography';
import axios from 'axios';
import csvtojson from 'csvtojson';
import React from 'react';

import { CustomLink } from './CustomLink';

const fetchData = async () => {
    const response = await axios.get(
        "https://docs.google.com/spreadsheets/d/e/2PACX-1vR7giEwAym5W8D-FX9I13fJo3xDS7QkGWAzi1c9WT8hkPx1R5qbr-3PicaW7kxegFywMytm1NOmqBdZ/pub?gid=1740377975&single=true&output=csv"
    );
    const rows = await csvtojson().fromString(response.data);
    const formattedData = {};

    rows.forEach((row, index) => {
        Object.keys(row).forEach((key) => {
            const [keyName, lang] = key.split("-");
            if (!formattedData[keyName]) {
                formattedData[keyName] = { kor: {}, eng: {} };
            }
            if (index === 0) {
                formattedData[keyName][lang].title = row[key];
                formattedData[keyName][lang].items = [];
            } else if (formattedData[keyName][lang] && row[key]) {
                formattedData[keyName][lang].items.push(row[key]);
            }
        });
    });
    return formattedData;
};

fetchData().then((data) => console.log(data));

